const initialState = {
    player1: '',
    player2: '',
    player1Icon: '',
    player2Icon: '',
    field: [
        [null, null, null],
        [null, null, null],
        [null, null, null]
    ],
    currentPlayer: 1,
    winner: null,
    score: {player1: 0, player2: 0}
}

const reducer = (state = initialState, action) => {

    switch (action.type) {
        case 'MOVE':
            const updatedField = [...state.field];
            if (state.currentPlayer === 1 && updatedField[action.payload.rowIndex][action.payload.cellIndex] === null) {
                updatedField[action.payload.rowIndex][action.payload.cellIndex] = 'X';
                return {
                    ...state,
                    field: [...updatedField],
                    winner: checkWin(updatedField, 'X') === 'tie' ? 'tie' : checkWin(updatedField, 'X') ? state.player1 : null,
                    currentPlayer: 2
                    }
            } else if (state.currentPlayer === 2 && updatedField[action.payload.rowIndex][action.payload.cellIndex] === null) {
                updatedField[action.payload.rowIndex][action.payload.cellIndex] = 'O';
                return {
                    ...state,
                    field: [...updatedField],
                    winner: checkWin(updatedField, 'O') === 'tie' ? 'tie' : checkWin(updatedField, 'O') ? state.player2 : null,
                    currentPlayer: 1
                };
            }
            break;
        case 'SAVE_NAME':
            if (!state.player1) {
                return {
                    ...state,
                    player1: action.payload
                }
            } else if (!state.player2) {
                return {
                    ...state,
                    player2: action.payload
                }
            }
            break;
        case 'CHOOSE_EMOJI':
            if (state.player1 && !state.player1Icon) {
                return {
                    ...state,
                    player1Icon: action.payload
                }
            } else if (state.player2 && !state.player2Icon) {
                return {
                    ...state,
                    player2Icon: action.payload
                }
            }
            break;
        case 'RESTART_GAME':
            if (state.winner === state.player1) {
                const updateScore = state.score.player1 + 1;
                console.log(state.winner === state.player1)
                console.log(state.player1)
                return {
                    ...state,
                    field: [
                        [null, null, null],
                        [null, null, null],
                        [null, null, null]
                    ],
                    currentPlayer: 1,
                    winner: null,
                    score: {...state.score, player1: updateScore}
                }
            } else if (state.winner === state.player2) {
                const updateScore = state.score.player2 + 1;
                return {
                    ...state,
                    field: [
                        [null, null, null],
                        [null, null, null],
                        [null, null, null]
                    ],
                    currentPlayer: 1,
                    winner: null,
                    score: {...state.score, player2: updateScore}
                }
            } else {
                return {
                    ...state,
                    field: [
                        [null, null, null],
                        [null, null, null],
                        [null, null, null]
                    ],
                    currentPlayer: 1,
                    winner: null
                }
            }
            break;
        case 'RESET_ALL':
            return {
                player1: '',
                player2: '',
                player1Icon: '',
                player2Icon: '',
                field: [
                    [null, null, null],
                    [null, null, null],
                    [null, null, null]
                ],
                currentPlayer: 1,
                winner: null,
                score: {player1: 0, player2: 0}
            }
        default:
            return state;
    }
}

const checkWin = (fieldToCheck, player) => {
    if (fieldToCheck[1][1] === player) { //check diagonal
        if (fieldToCheck[0][0] === player && fieldToCheck[2][2] === player) return true;
        if (fieldToCheck[0][2] === player && fieldToCheck[2][0] === player) return true;
    }
    for (let i = 0; i < 3; i++) {
        if (fieldToCheck[i].every(cell => cell === player)) return true; // check rows
        if (fieldToCheck.every(row => row[i] === player)) return true; // check columns
    }
    if (fieldToCheck.every(row => row.every(cell => cell))) return 'tie';
    return false;
}

export default reducer;